import { CATEGORY, EVENT, META, STEP_ONE } from "../../models/events.model";
import {
  EVENT_LIST,
  CATEGORY_LIST,
  GET_RECORD,
  OPEN_MODAL,
  OPEN_DELETE_MODAL,
  STEP_ONE_ACTION,
} from "./action";

const initialState: {
  EventData: EVENT[];
  CategoryData: CATEGORY[];
  meta: META;
  EventRecord: EVENT;
  openModal: boolean;
  opendeletemodal: boolean;
  deletedRecord: EVENT;
  stepOne: STEP_ONE | null;
} = {
  EventData: [],
  meta: {},
  CategoryData: [],
  EventRecord: {},
  openModal: false,
  opendeletemodal: false,
  deletedRecord: {},
  stepOne: null,
};

export default function EventReducer(
  state = initialState,
  action: {
    type: string;
    payload: {
      data?: EVENT[];
      meta?: META;
      CategoryData?: CATEGORY[];
      EventRecord: EVENT;
      openModal: boolean;
      opendeletemodal: boolean;
      deletedRecord: EVENT;
      stepOne?: STEP_ONE | null;
    };
  }
): {
  EventData: EVENT[];
  CategoryData: CATEGORY[];
  meta: META;
  EventRecord: EVENT;
  openModal: boolean;
  opendeletemodal: boolean;
  deletedRecord: EVENT;
  stepOne: STEP_ONE | null;
} {
  switch (action.type) {
    case EVENT_LIST:
      return {
        ...state,
        EventData: action.payload.data!,
        meta: action.payload.meta!,
      };
    case CATEGORY_LIST:
      return {
        ...state,
        CategoryData: action.payload.CategoryData!,
      };
    case GET_RECORD:
      return {
        ...state,
        EventRecord: action.payload.EventRecord,
        openModal: true,
      };
    case OPEN_MODAL:
      return {
        ...state,
        openModal: action.payload.openModal,
      };
    case OPEN_DELETE_MODAL:
      return {
        ...state,
        opendeletemodal: action.payload.opendeletemodal,
        deletedRecord: action.payload.deletedRecord,
      };
    case STEP_ONE_ACTION:
      return {
        ...state,
        stepOne: action.payload?.stepOne!,
      };

    default:
      return state;
  }
}
